<template>
    <div>

        <div style="margin: 10px">
            <span>会员信息</span>
            <el-input v-model="inputData" placeholder="请输入内容" style="width: 300px"></el-input>
            <span class="left-normal-margin">虚拟币类型</span>
            <el-select v-model="virtualTypeValue" placeholder="请选择">
                <el-option
                        v-for="item in virtualType"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
            </el-select>
            <span class="left-normal-margin">日期</span>
            <el-date-picker
                    v-model="dateValue"
                    type="date"
                    @change="handleTimeChange"
                    placeholder="选择日期">
            </el-date-picker>
            <span class="left-normal-margin">价格</span>
            <el-input v-model="inputPrice" placeholder="请输入价格" style="width: 180px"></el-input>
            <span class="left-normal-margin">状态</span>
            <el-select v-model="virtualStatusValue" placeholder="请选择" style="width: 120px">
                <el-option
                        v-for="item in virtualStatus"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
            </el-select>
            <span class="left-normal-margin">类型</span>
            <el-select v-model="typeValue" placeholder="请选择" style="width: 120px">
                <el-option
                        v-for="item in type"
                        :key="item.value"
                        :label="item.label"
                        :value="item.value">
                </el-option>
            </el-select>
            <el-button class="left-normal-margin" type="primary" size="small" @click.stop="query">查询</el-button>
        </div>

        <el-table
                :data="tableData"
                :border="false"
                style="width: 100%">
            <el-table-column
                    prop="date"
                    label="FID"
                    width="150">
            </el-table-column>
            <el-table-column
                    prop="name"
                    label="登录名"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="province"
                    label="会员昵称"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="city"
                    label="会员真实姓名"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="address"
                    label="虚拟币类型"
                    width="300">
            </el-table-column>
            <el-table-column
                    prop="zip"
                    label="交易类型"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="zip"
                    label="状态"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="zip"
                    label="单价"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="zip"
                    label="数量"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="zip"
                    label="邮编"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="zip"
                    label="未成交数量"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="zip"
                    label="已成交数量"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="zip"
                    label="总金额"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="zip"
                    label="成交总金额"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="zip"
                    label="修改时间"
                    width="120">
            </el-table-column>
            <el-table-column
                    prop="zip"
                    label="创建时间"
                    width="120">
            </el-table-column>
        </el-table>
    </div>
