<script>

    export default {
        data() {
            return {
                tableData: [{
                    date: 'xxxx',
                    name: 'xxxx',
                    province: 'xxx',
                    city: 'xxx',
                    address: 'xxxxxxxxxxxxxx',
                    zip: 'xxxxx'
                }, {
                    date: 'xxxx',
                    name: 'xxxx',
                    province: 'xxx',
                    city: 'xxx',
                    address: 'xxxxxxxxxxxxxx',
                    zip: 'xxxxx'
                }, {
                    date: 'xxxx',
                    name: 'xxxx',
                    province: 'xxx',
                    city: 'xxx',
                    address: 'xxxxxxxxxxxxxx',
                    zip: 'xxxxx'
                }, {
                    date: 'xxxx',
                    name: 'xxxx',
                    province: 'xxx',
                    city: 'xxx',
                    address: 'xxxxxxxxxxxxxx',
                    zip: 'xxxxx'
                }, {
                    date: 'xxxx',
                    name: 'xxxx',
                    province: 'xxx',
                    city: 'xxx',
                    address: 'xxxxxxxxxxxxxx',
                    zip: 'xxxxx'
                }, {
                    date: 'xxxx',
                    name: 'xxxx',
                    province: 'xxx',
                    city: 'xxx',
                    address: 'xxxxxxxxxxxxxx',
                    zip: 'xxxxx'
                }, {
                    date: 'xxxx',
                    name: 'xxxx',
                    province: 'xxx',
                    city: 'xxx',
                    address: 'xxxxxxxxxxxxxx',
                    zip: 'xxxxx'
                }],
                inputData: '',
                virtualType: [{
                    value: '1',
                    label: '全部'
                }, {
                    value: '2',
                    label: '爱心币'
                }, {
                    value: '3',
                    label: '美业币'
                }],
                virtualTypeValue: '1',
                dateValue: '',
                inputPrice: '',
                virtualStatusValue: '1',
                virtualStatus: [{
                    value: '1',
                    label: '全部'
                }, {
                    value: '2',
                    label: '未成交'
                }, {
                    value: '3',
                    label: '部分成交'
                }, {
                    value: '4',
                    label: '完全成交'
                }, {
                    value: '5',
                    label: '用户撤销'
                }],
                typeValue: '1',
                type: [{
                    value: '1',
                    label: '全部'
                }, {
                    value: '2',
                    label: '买入'
                }, {
                    value: '3',
                    label: '卖出'
                }]
            }
        },
        methods: {
            // 查询
            query() {
                console.log('query');
            },
            // 时间改变钩子
            handleTimeChange(time) {
                console.log(time);
                this.dateValue = time;
            }
        }
    }

</script>

<style rel="stylesheet/scss" lang="scss" scoped>
    .left-normal-margin {
        margin-left: 10px;
    }


</style>


